body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    font-family: 'Roboto', sans-serif;
    min-height: 100vh;
    background: rgb(227,53,13);
    background: linear-gradient(122deg, rgba(227,53,13,1) 0%, rgba(49,49,49,1) 62%, rgba(49,49,49,1) 100%);
    background: repeating-linear-gradient(
    315deg,
  #313131,
  #313131 10px,
  #444444 10px,
  #444444 40px
);
  }
  
  .search-form{
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .search-bar{
    width: 50%;
    border: none;
    padding: 0.73vw;
  }
  
  .search-button{
    background: #E3350D;
    border: none;
    padding: 0.73vw 1.46vw;
    color: white;
    font-weight: bold;
  }
  
  .search-button:hover{
    background: #861d06;
  }
  
  .card-holder{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

.card{
    border-radius: 0.74vw;
    box-shadow: 0px 0.37vw 1.48vw #1818189a;
    margin: 1.48vw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: rgb(255, 255, 255);
    align-items: center;
    min-width: 40%;
    padding: 0.74vw;
}

.pokeName{
    color: #212121;
    font-size: 36px;
    margin: 18px 0 0 0;
    text-transform: uppercase;
}

.listTitle{
    color: #212121;
    font-size: 20px;
    font-weight: bold;
    margin: 25px;
    text-transform: uppercase;
}

.lists{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    text-align: center;
    justify-content: space-around;
}

.statList , .typeList, .spriteList, .moveList, .AppearanceList, .abilityList{
    list-style: none;
    text-align: center;
    text-transform: uppercase;
    background-color: #30a7d7;
    color: #fff;
    border-radius: 10px;
    padding: 5px;
    margin: 10px;
    box-shadow: 0 0 10px black;
}

.moveList , .AppearanceList{
    max-height: 200px;
    overflow: hidden; 
    overflow-y: auto;
}

.AppearanceList li{
    color: white;
    text-shadow: rgb(49, 49, 49) 2px 0px 0px, rgb(49, 49, 49) 1.75517px 0.958851px 0px, rgb(49, 49, 49) 1.0806px 1.68294px 0px, rgb(49, 49, 49) 0.141474px 1.99499px 0px, rgb(49, 49, 49) -0.832294px 1.81859px 0px, rgb(49, 49, 49) -1.60229px 1.19694px 0px, rgb(49, 49, 49) -1.97998px 0.28224px 0px, rgb(49, 49, 49) -1.87291px -0.701566px 0px, rgb(49, 49, 49) -1.30729px -1.5136px 0px, rgb(49, 49, 49) -0.421592px -1.95506px 0px, rgb(49, 49, 49) 0.567324px -1.91785px 0px, rgb(49, 49, 49) 1.41734px -1.41108px 0px, rgb(49, 49, 49) 1.92034px -0.558831px 0px;
    padding: 5px;
    margin-top: 5px;
    border-radius: 5px;
}

.typeList{
    box-shadow: none;
    background-color: transparent;
    display: flex;
    width: 100%;
}

.typeList li{
    border-radius: 5px;
    margin: 0.5em 0.5em 0 0;
    padding: 0.2em 1.3em;
    text-align: center;
}

.statList{
    text-align: left;
}

.spriteList{
    color: #212121;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    background-color: rgba(0, 0, 0, 0.116);
    border-radius: 10px;
}

.spriteListItem{
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    padding: 10px;
    margin: 10px;
}

.spriteTitle{
    text-transform: uppercase;
    font-weight: bold;
}

.sprite{
    height: 100px;
    width: 100px;
    border-radius: 50%;
    border: solid black;
}

.fireType{
    background: #F08030;
}



@media (max-width: 320px) 
{
    .spriteList{
        
        grid-template-columns: 1fr;
    }
}

@media (max-width: 600px) 
{
    .spriteList{
        
        grid-template-columns: 1fr 1fr;
    }
}

